                    <b-form-group id="input-group-11" label="Building Status:" label-for="input-11">
                      <b-form-input id="input-8" v-model="form.buildingStatus" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-12" label="Status:" label-for="input-12">
                      <b-form-input id="input-8" v-model="form.status" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
