                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-13" label="Design:" label-for="input-13">
                      <b-form-select class="dropdown-box" v-model="form.design" :options="probabilityOptions"></b-form-select>
                    </b-form-group>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-14" label="Cost:" label-for="input-14">
                      <b-form-input id="input-6" v-model="form.cost" type="number" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-15" label="Antenna #/Small Cell #:" label-for="input-15">
                      <b-form-input id="input-8" v-model="form.antenna" type="number" required placeholder=""></b-form-input>
                    </b-form-group>
