                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-16" label="Monthly revenue/site:" label-for="input-16">
                      <b-form-input id="input-8" v-model="form.revenue" type="number" required placeholder=""></b-form-input>
                    </b-form-group>
