                </b-row>
                <b-row>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-18" label="Remarks:" label-for="input-18">
                      <b-form-input id="input-6" v-model="form.remarks" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-19" label="CRM No:" label-for="input-19">
                      <b-form-input id="input-8" v-model="form.crmNo" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-20" label="# of Small Cell:" label-for="input-20">
                      <b-form-input id="input-8" v-model="form.smallCell" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                  <b-col lg="3" md="3" sm="12">
                    <b-form-group id="input-group-17" label="Update By:" label-for="input-17">
                      <b-form-input id="input-8" v-model="form.updateBy" type="text" required placeholder=""></b-form-input>
                    </b-form-group>
                  </b-col>
                </b-row>
                <b-button variant="primary" id="nextBtn" @click="goNext">NEXT &#x226B;</b-button>
              </div>
              <div id="formSummary" v-if="formPreview">
                <div>FORM SUMMARY</div>
                <b-row>
                  <b-col lg="6" md="6" sm="12">
                    <table>
                      <tr id="tableHeader">
                        <td>PROPERTY</td>
                        <td>VALUE</td>
                      </tr>
                      <tr>
                        <td>EASI CODE</td>
                        <td>{{ this.form.easiCode }}</td>
                      </tr>
                      <tr>
                        <td>TYPE</td>
                        <td>{{ this.form.type }}</td>
                      </tr>
                      <tr>
                        <td>SITE NAME</td>
                        <td>{{ this.form.siteName }}</td>
                      </tr>
                      <tr>
                        <td>SITE TYPE</td>
                        <td>{{ this.form.siteType }}</td>
                      </tr>
                      <tr>
                        <td>DEVELOPER</td>
                        <td>{{ this.form.developer }}</td>
                      </tr>
                      <tr>
                        <td>PROJECT CATAGORY</td>
                        <td>{{ this.form.projectCatagory }}</td>
                      </tr>
                      <tr>
                        <td>CUSTOMER NAME</td>
                        <td>{{ this.form.customerName }}</td>
                      </tr>
                      <tr>
                        <td>PROBABILITY</td>
                        <td>{{ this.form.probability }}</td>
                      </tr>
                      <tr>
                        <td>FORECASTED RFAI</td>
                        <td>{{ this.form.forecastedRFAI }}</td>
                      </tr>
                      <tr>
                        <td>BUILDING STATUS</td>
                        <td>{{ this.form.buildingStatus }}</td>
                      </tr>
                    </table>
                  </b-col>
                  <b-col lg="6" md="6" sm="12">
                    <table>
                      <tr id="tableHeader">
                        <td>PROPERTY</td>
                        <td>VALUE</td>
                      </tr>
                      <tr>
                        <td>STATUS</td>
                        <td>{{ this.form.status }}</td>
                      </tr>
                      <tr>
                        <td>DESIGN</td>
                        <td>{{ this.form.design }}</td>
                      </tr>
                      <tr>
                        <td>COST</td>
                        <td>{{ this.form.cost }}</td>
                      </tr>
                      <tr>
                        <td>ANTENNA</td>
                        <td>{{ this.form.antenna }}</td>
                      </tr>
                      <tr>
                        <td>REVENUE</td>
                        <td>{{ this.form.revenue }}</td>
                      </tr>
                      <tr>
                        <td>FY19 FORECAST</td> 
                        <td>{{ this.form.fy19Forecast }}</td>
                      </tr>
                      <tr>
                        <td>REMARKS</td>
                        <td>{{ this.form.remarks }}</td>
                      </tr>
                      <tr>
                        <td>CRM NO.</td>
                        <td>{{ this.form.crmNo }}</td>
                      </tr>
                      <tr>
                        <td>SMALL CELL</td>
                        <td>{{ this.form.smallCell }}</td>
                      </tr>
                      <tr>
                        <td>UPDATE BY</td>
                        <td>{{ this.form.updateBy }}</td>
                      </tr>
                    </table>
                  </b-col>
                  <b-button variant="primary" id="infoUploadBtn" @click="formUpload">UPLOAD</b-button>
                  <b-button variant="primary" id="backBtn" @click="backToEdit">BACK TO EDIT</b-button>
                </b-row>
              </div>
            </b-form>
          </b-container>
        </div>
