</template>
<script>
import { db } from '../firebase.js';
import Vue from 'vue';

export default {
  props: ['userID', 'password'],
  data() {
    return {
      logRetriveData: [],
      loader: true,
      dataLoadSignal: false,
      logCard: true,
      infoForm: true,
      formPreview: false,
      bannerImage: require('../assets/edotco_group_banner.png'),
      thumbnail_image: require('../assets/thumbnail_image.png'),
      form: {
        easiCode: "",
        type: "",
        siteName: "",
        siteType: "",
        developer: "",
        projectCatagory: "",
        customerName: "",
        probability: "",
        forecastedRFAI: "",
        buildingStatus: "",
        status: "",
        design: "",
        cost: "",
        antenna: "",
        revenue: "",
        fy19Forecast: "",
        remarks: "",
        crmNo: "",
        smallCell: "",
        updateBy: "user"
      },
      typeOptions: [
        { value: 'IBS', text: 'IBS' },
        { value: 'Small Cell', text: 'Small Cell' },
        { value: 'Others', text: 'Others' },
        { value: 'BTS Hotel', text: 'BTS Hotel' }
      ],
      siteTypeOptions: [
        { value: 'New', text: 'New' },
        { value: 'Existing', text: 'Existing' }
      ],
      projectCatagoryOptions: [
        { value: 'B2S', text: 'B2S' },
        { value: 'Colo', text: 'Colo' },
        { value: 'Upgrade', text: 'Upgrade' }
      ],
      probabilityOptions: [
        { value: 'Commit', text: 'Commit' },
        { value: 'Upside < 50%', text: 'Upside < 50%' },
        { value: 'Upside > 50%', text: 'Upside > 50%' }
      ]
    }
  },
  firestore(){
    return {
      logRetriveData: db.collection('e.co_sales_log')
    }
  },
  methods: {
    setCookie: function(cookie_name, cookie_value, exdays){
      var d = new Date();
      d.setTime(d.getTime() + (exdays*24*60*60*1000));
      var expires = "expires="+ d.toUTCString();
      document.cookie = cookie_name + "=" + cookie_value + ";" + expires + ";path=/";
      return true;
    },
    onSubmit(evt) {
      evt.preventDefault()
      // alert(JSON.stringify(this.form))
      if(this.userID == "user" && this.password == "1234"){
        this.logCard = false;
      }else{
        alert("Wrong UserID or Password!")
        this.userID = "";
        this.password = "";
      }
      this.setCookie('user_id', this.userID , 1);
      this.setCookie('password', this.password , 1);
      location.reload();
    },
    goNext: function(){
      this.infoForm = false;
      this.formPreview = true;
    },
    backToEdit: function(){
      this.infoForm = true;
      this.formPreview = false;
    },
    formUpload: function(){
      alert("Database Connection Error!")
    }
  },
  computed: {
    
  },
  watch: {
    'logRetriveData': function(){
      if(this.logRetriveData.length > 0){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  },
  mounted: function() {
    this.$nextTick(function () {
      if(this.userID == "user" && this.password == "1234"){
        this.logCard = false;
      }
    })
  }
}
</script>
<style scoped>
  /** Mainbody **/
  #mainBody{
    height: 100vh;
    background-position: center;
  }
  #mainBody>div{
    border-width: 0 1px 1px 1px;
    border-color: #eee;
    border-style: solid;
  }
  #bodyContent{
    padding-top: 59px;
  }
  #logCard{
    width: 100%;
    background: #eee;
    position: absolute;
    float: left;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    /* -webkit-box-shadow: 0 6px 8px rgba(144,172,201,.03), 0 1px 2px rgba(102,119,136,.3); */
    /* box-shadow: 0 6px 8px rgba(144,172,201,.03), 0 1px 2px rgba(102,119,136,.3); */
    border: 1px solid #efefef;
    border-radius: 3px 3px 0 0;
  }
  #logCardHeader{
    background-color: #fff;
    padding: 15px;
  }
  #logCardForm{
    padding: 15px;
  }
  .form-control{
    -webkit-box-shadow: none;
    font-size: 14px; 
  }
  .dropdown-box{
    -webkit-box-shadow: none;
    font-size: 14px;
  }
  #logInBtn{
    background-color: #aeaeae;
    color: #fff;
    border: none;
    font-size: 14px;
    height: 33px;
  }
  #logCardFooterImage{
    width: 100%;
  }

  /** FORM CARD */
  #formCard{
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: #f2f2f2;
  }
  #formHeader{
    border-bottom: 1px solid #ced4da;
    padding: 0 0 10px 0;
    margin-bottom: 10px;
  }
  #infoForm{
    margin-bottom: 40px;
  }
  #nextBtn{
    background-color: #0c754a;
    color: #fff;
    border: none;
    font-size: 14px;
    height: 33px;
    width: 100px;
  }
  #infoUploadBtn{
    background-color: #0c754a;
    color: #fff;
    border: none;
    font-size: 14px;
    height: 33px;
    width: 100px;
    margin: 15px 15px 0;
  }
  #backBtn{
    background-color: #dc3545;
    color: #fff;
    border: none;
    font-size: 14px;
    height: 33px;
    width: 150px;
    margin: 15px 0 0;
  }
  /** MODAL */
  #formImport{
    float: right;
    cursor: pointer;
  }
  #uploadBtn{
    background-color: #0c754a;
    color: #fff;
    border: none;
    margin-top: 10px;
    float: right;
    width: 100%;
    font-size: 14px;
  }

  /** Table */
  #formSummary{
    margin-top: 10px;
    border: 1px solid #e9ecef;
    padding: 10px 15px 15px 15px;
    background-color: gray;
    color: #fff;
  }
  table, td, th {  
    border: 1px solid #ddd;
    text-align: left;
    margin-top: 10px;
  }

  table {
    border-collapse: collapse;
    width: 100%;
    font-size: 12px;
  }
  th, td {
    padding: 5px;
  }
  #tableHeader{
    background-color: #0c754a;
  }

  /** Page Media Query Start */
  @media (min-width: 1200px){
    #logCard{
      width: 45%;
    }
  }
  @media screen and (max-width: 991px) {
    #logCard{
      width: 45%;
    }
  }
  @media screen and (max-width: 767px) {
    #logCard{
      width: 100%;
      height: 96.4%
    }
  }
  @media screen and (max-width: 479px) {
    #logCard{
      width: 100%;
      height: 96.4%
    }
  }
  /** Page Media Query End */
</style>


