<template>
  <div id="app">
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-navbar-brand href="#" id="navbarBrand" to="/">
        <img :src="brandImage" />EDOTCO
      </b-navbar-brand>
      <b-navbar-nav class="ml-auto">
        <span>{{ this.title }}</span>
        <b-dropdown id="logoutBtn" text="" variant="default" v-if="logoutBtn">
          <b-dropdown-item @click="logout">LOG OUT</b-dropdown-item>
        </b-dropdown>
      </b-navbar-nav>
    </b-navbar>
    <!-- Navbar End -->
    <router-view :userID="userId" :password="password"></router-view>
    <div id="footer">
      <span>edotco Group</span>
      <span id="footerRight">a communication's infrastructure service company</span>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
// import VueTypedJs from 'vue-typed-js';
// Vue.use(VueTypedJs);
// <vue-typed-js :strings="TYPING" :loop="true" :showCursor="false" id="navTypeJs">
//   <span class="typing"></span>
// </vue-typed-js>
export default {
  name: 'app',
  data: function(){
    return {
      title: '',
      brandImage: require('./assets/logo_edotco.svg'),
      userId: this.getCookie('user_id'),
      password: this.getCookie('password'),
      logoutBtn: false
    }
  },
  methods: {
    setCookie: function(cookie_name, cookie_value, exdays){
      var d = new Date();
      d.setTime(d.getTime() + (exdays*24*60*60*1000));
      var expires = "expires="+ d.toUTCString();
      document.cookie = cookie_name + "=" + cookie_value + ";" + expires + ";path=/";
      return true;
    },
    getCookie: function(cookie_name){
      var name = cookie_name + "=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(';');
      for(var i = 0; i <ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') {
              c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
              return c.substring(name.length, c.length);
          }
      }
      return "";
    },
    logout: function(){
      this.setCookie('user_id', "" , 1);
      this.setCookie('password', "" , 1);
      location.reload();
    }
  },
  mounted: function() {
    this.$nextTick(function () {
      if(this.userId == "user" && this.password == "1234"){
        this.logoutBtn = true;
      }
    })
  }
}
</script>
<style>
  /** Global Fonts */
  * {
    font-family: 'Raleway', sans-serif;
    /* font-family: 'Hind Siliguri', sans-serif;
    font-weight: 400; */
  }
  /** Hide Scroll Bar */
  ::-webkit-scrollbar { 
    display: none; 
  }
  /** Header */
  h1,
  h2,
  h3,
  h4,
  h5 {
    line-height: 1.2;
    margin-top: 0;
    margin-bottom: 1.5rem;
    font-weight: 300;
    letter-spacing: -.03rem; 
  }
  /** Navbar **/
  #navbar{
    background-color: #fff;
    height: 60px;
    padding: 0 10px;
    border-bottom: 1px solid #e9ecef;
  }
  #navbar img{
    width: 55px;
  }
  #navbarBrand{
    color: #fff;
  }
  #logoutBtn{
    background-color: #aeaeae;
    border: none;
    width: 38px;
    height: 36px;
    border-radius: 10%;
  }
  .btn{
    color: #fff;
  }
  label{
    font-size: 14px;
  }
  .form-text {
    font-size: 11px;
    font-style: italic;
  }
  /* Footer */
  #footer{
    background-color: #0c754a;
    color: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: justify;
    border-width: 0 0 0 0 !important;
    border-style: solid;
    border-color:#efefef;
    padding: 0 !important;
    font-size: 15px;
    padding: 10px !important;
  }
  #footerRight{
    float: right;
    display: none;
    text-transform: capitalize;
  }
  /** LOADER START */
  /** Loader Custom */
  .lds-ellipsis, .lds-ring{
    z-index: 1;
    position:absolute;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    top: 150px;
  }
  /** Loader Default */
  .lds-ring {
    display: inline-block;
    width: 64px;
    height: 64px;
  }
  #loaderText{
    position: relative; 
    top: 100px; 
    color: #6c757d;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 2px solid #6c757d;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #6c757d transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  /** LOADER END */

  /** Page Media Query Start */
  @media (min-width: 1200px){
    #footerRight{
      display: block;
    }
  }
  @media screen and (max-width: 991px) {
    
  }
  @media screen and (max-width: 767px) {
    
  }
  @media screen and (max-width: 479px) {
    
  }
</style>



